// External
import React, { useState, useEffect } from 'react';

// Material UI
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';

// Owned
import { useStyles } from '../styles';
import HorizontalLogo from '../HorizontalLogo';
import { Request, Messages } from '../../../../shared';

import { useTranslation } from 'react-i18next';

export default function Welcome(props) {
    const classes = useStyles();
    const { t } = useTranslation();

    const [version, setVersion] = useState('');
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        async function getVersion() {
            try {
                const result = await new Request(Messages.Generic.GetVersion).sendRequest();
                const version = result.message.payload.version;   
                setVersion(version); 
            } catch(e) {
                setLoading(false);
            }
            setLoading(false);
        }
        
        try {
            getVersion();
        } catch(e) {
            setVersion("");
            setLoading(false);
        }
    }, []);

    return(
        <Container component="main" maxWidth="md">
            <CssBaseline />
            {loading ?
                <div className={classes.paper}>
                    <CircularProgress />
                </div>
            :
                <div className={classes.welcomeHeader}>
                    <div className={classes.imgHeader}>
                        <HorizontalLogo light={true} width={"300px"} />
                    </div>
                    <Typography className={classes.testExtensionTitle} component="h2" variant="h5">
                        {t('onboard.welcome.title', { version: version })}
                    </Typography>
                    {props.configDone ? null 
                    :
                    <Typography className={classes.testExtensionSubtitle} component="h1" variant="h6">
                        {t("onboard.welcome.description")}
                    </Typography>
                    }
                </div>            
            }

        </Container>
    )
}