import React, { useState } from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';

// Icons
import QuestionMarkIcon from '@mui/icons-material/QuestionMark';
import PersonIcon from '@mui/icons-material/Person';
import SettingsIcon from '@mui/icons-material/Settings';
import SettingsRemoteIcon from '@mui/icons-material/SettingsRemote';

//Owned
import Info from './Info/Info';
import Login from './Login/Login';
import Preferences from './Preferences/Preferences';
import Analytics from './Analytics/Analytics';
import { useStyles } from '../styles';

import { useTranslation } from 'react-i18next';

export default function Configuration(props) {
  const classes = useStyles();
  const { t } = useTranslation();

  const [activeStep, setActiveStep] = useState(0);

  const steps = [
    t("onboard.configuration.steps.how_to"),
    t("onboard.configuration.steps.user_account"),
    t("onboard.configuration.steps.cookie_preferences"),
    t("onboard.configuration.steps.data_collection")
  ];

  const stepIcons = [
    <QuestionMarkIcon fontSize="large" color="secondary"/>,
    <PersonIcon fontSize="large" color="secondary"/>,
    <SettingsIcon fontSize="large" color="secondary"/>,
    <SettingsRemoteIcon fontSize="large" color="secondary"/>
  ];
  
  const stepContent = [
    <Info />,
    <Login />,
    <Preferences />,
    <Analytics />
  ];

  function ShowIcon(props) {
    if (props.index === activeStep) {
      stepIcons[props.index].props.color = "secondary";
    } else {
      stepIcons[props.index].props.color = "info";
    }

    return (
      <div className={classes.gridIcon}>
        {stepIcons[props.index]}
        <br />
        <Typography className={classes.testExtensionSubtitle}>
          {steps[props.index]}
        </Typography>
      </div>
    );
  }

  function NavigationButtons(props) {
    return (
      <div className={classes.stepperNavigation}>
        <Grid container spacing={2} style={{textAlign:"left"}}>
          {activeStep > 0 ?
            <Grid item xs={6}>
              <Button color="secondary" variant="contained" onClick={() => setActiveStep(activeStep - 1)}>
                {t("onboard.configuration.buttons.previous")}
              </Button>
            </Grid>
          : null}
          {activeStep < steps.length - 1 ?
            <Grid item xs={activeStep === 0 ? 12 : 6} style={{textAlign:"right"}}>
                <Button color="secondary" variant="contained" onClick={() => setActiveStep(activeStep + 1)}>
                  {t("onboard.configuration.buttons.next")}
                </Button>
            </Grid>
          : activeStep === steps.length - 1 ?
              <Grid item xs={6} style={{textAlign:"right"}}>
                <Button color="secondary" variant="contained" onClick={() => { props.setConfigDone(true) }}>
                  {t("onboard.configuration.buttons.finish")}
                </Button>
            </Grid>
          : null}      
        </Grid>
      </div>
    );
  }

  return (
    <div className={classes.configurationStepper}>
      <Container component="main" maxWidth="md">
        <Grid container spacing={2} >
          {steps.map((label, index) => (
                <Grid item xs={3} className={classes.gridIcon}>
                  {<ShowIcon label={label} index={index}/>}
                </Grid>
              ))}
        </Grid>
        <div className={classes.paper}>
          {stepContent[activeStep]}
        </div>
        <NavigationButtons setConfigDone={props.setConfigDone}/>
      </Container>
    </div>
    );
}