import React, { useState, useEffect } from "react";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";

import Confetti from 'react-dom-confetti';

import { useStyles } from '../styles';

import { useTranslation } from 'react-i18next';

const confettiConfig = {
    angle: 90,
    spread: 360,
    startVelocity: 20,
    elementCount: 70,
    dragFriction: 0.12,
    duration: 3000,
    stagger: 3,
    width: "10px",
    height: "10px",
    perspective: "500px",
    colors: ["#FDC737"]
};

export default function Done() {
    const classes = useStyles();
    const { t } = useTranslation();

    const [showConfetti, setShowConfetti] = useState(false);

    useEffect(() => {
        setTimeout(() => {
            setShowConfetti(true);
        }, 1000);
    }, []);

    return(
        <div className={classes.paper}>
            <Confetti active={showConfetti} config={confettiConfig} />
            <Typography className={classes.testExtensionSubtitle} component="h1" variant="h4">
                {t("onboard.done.title")} 
            </Typography>
            <Typography className={classes.testExtensionSubtitle} component="h1" variant="h6">
                {t("onboard.done.description")}
            </Typography>
            <Grid container spacing={2} style={{marginTop:"20px"}}>
                <Grid item xs={6} style={{textAlign: "right"}}>
                    <Button color="secondary" variant="contained" onClick={() => { chrome.runtime.openOptionsPage(); }}>
                        {t("onboard.done.buttons.open_settings")}
                    </Button>
                </Grid>
                <Grid item xs={6} style={{textAlign: "left"}}>
                    <Button color="secondary" variant="contained" onClick={() => { window.close(); }}>
                    {t("onboard.done.buttons.close_window")}
                    </Button>
                </Grid>
            </Grid>
        </div>
    )
}